.li_category:hover {
  border: 5px solid rgb(194, 165, 3);
  height: 30px;
  background-color: rgb(194, 165, 3);
  color: white;
}
.products_container {
  display: flex;

  justify-content: center;
  flex-wrap: wrap;
}

#user_name_products {
  text-align: center;
  color: white;
  margin: 0px;
}

#payment_btn {
  width: 120px;
  height: 30px;
  border: none;
  border-radius: 10px;
  background-color: white;
  font-size: 15px;
}

#category_hamburger {
  height: 40px;
  width: 40px;
}
.hamburger {
  margin: 3px;
  height: 10px;

  border-radius: 10px;
  background-color: white;
}
.payment_btn_container {
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 768px) {
  #category_hamburger {
    position: sticky;
    top: 20px;
  }
  .test{
    display: flex;
    justify-content: space-between;
  }
}
